import React from 'react'
import { inject, observer } from 'mobx-react';
import { Row, Select, Tabs, Button, DatePicker } from 'antd';
import StockTable from './StockTable'
import StockDialog from './StockDialog'
import moment from 'moment';

@inject('SparepartsStore')
@observer

class SparepartsPage extends React.Component {
  render() {
    const { modalVisible, columnsType, spareList, searchDate, spareName } = this.props.SparepartsStore
    const { Option } = Select
    const { TabPane } = Tabs
    const { RangePicker } = DatePicker;

    return <div>
      <Tabs activeKey={columnsType} onChange={this.changeTabs}>
        <TabPane tab={<div><i className="iconfont icon-list icon-spacing"></i>库存列表</div>} key="1"/>
        <TabPane tab={<div><i className="iconfont icon-recording icon-spacing"></i>出入库记录</div>} key="2" />
      </Tabs>
      <div className="tab-content-bg">
        <Row>
          <Select
            showSearch
            allowClear
            style={{ width: 250 }}
            optionFilterProp="children"
            placeholder='请选择备件'
            onChange={this.changeSpa}
            value={spareName}
            filterOption={(input, option) =>
              option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            } >
            {spareList.map(item => <Option key={item.id} value={columnsType === '1' ? item.spareName : item.id}>{item.spareName}</Option>)}
          </Select>
          {columnsType === '2' && <RangePicker style={{ marginLeft: 10 }} value={searchDate.length === 2 ? [moment(searchDate[0]), moment(searchDate[1])] : ""} placeholder={['开始时间', '结束时间']} onChange={this.changeDate} />}
          <Button type='primary' style={{ marginLeft: 10 }} onClick={this.onSearchData} className="btn-border-purple">查询</Button>
          {columnsType === '1' && <span>
            <Button style={{ marginLeft: 10 }} onClick={e => this.openModal(1)} type='primary' >
              <i className='iconfont icon-input icon-spacing' style={{fontSize:'12px'}}></i>
              入库
            </Button>
            <Button style={{ marginLeft: 10 }} onClick={e => this.openModal(2)} type='primary' >
              <i className='iconfont icon-output icon-spacing' style={{fontSize:'12px'}}></i>
              出库
            </Button>
            <Button style={{ marginLeft: 10 }} onClick={e => this.openModal(3)} type='primary' >
              <i className='iconfont icon-back icon-spacing' style={{fontSize:'12px'}}></i>
              还库
            </Button>
          </span>}
        </Row>
        <StockTable history={this.props.history} />
        {modalVisible && <StockDialog />}
      </div>
    </div>;
  }
  changeSpa = (e) => {
    this.props.SparepartsStore.spareName = e
  }
  onSearchData = () => {
    const { getData, PageInfo, searchDate, spareName, columnsType, getLogData } = this.props.SparepartsStore
    columnsType === '1' ? getData(2, { spareName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize }) : getLogData({ spareId: spareName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, startDate: searchDate[0], endDate: searchDate[1] })
  }
  changeDate = (e, formatDate) => {
    console.log(e, formatDate)
    this.props.SparepartsStore.searchDate = formatDate
  }
  changeTabs = (e) => {
    this.props.SparepartsStore.columnsType = e
    this.props.SparepartsStore.searchDate = []
    this.props.SparepartsStore.spareName = undefined
    this.props.SparepartsStore.tableData = []
    this.props.SparepartsStore.PageInfo = { pageIndex: 1, pageSize: 10 }
    const { getData, PageInfo, searchDate, spareName, columnsType, getLogData } = this.props.SparepartsStore
    columnsType === '1' ? getData(2, { spareName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize }) : getLogData({ spareId: spareName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, startDate: searchDate[0], endDate: searchDate[1] })
  }
  openModal = (type) => {
    this.props.SparepartsStore.modalTitle = type

    this.props.SparepartsStore.modalChange(0)
  }
  componentWillUnmount() {
    this.props.SparepartsStore.clearData()
  }
}

export default SparepartsPage